  align-items: center;
}
.product__item .hexagon {
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
}
.product__item .hexagon polygon {
  fill: transparent;
  stroke-width: 4;
  stroke: #6fbcbc;
  transition: 0.3s;
}
.product__item .icon {
  fill: #6fbcbc;
  transition: 0.3s;
}
.product__item .name {
  font-size: 19px;
  font-weight: 400;
  color: #fff;
  position: absolute;
  bottom: 19px;
  left: 0;
  right: 0;
  margin: 0 auto;
  line-height: 0.9;
  max-width: 110px;
  text-align: center;
}
.product__item_empty {
  opacity: 0.2;
  position: absolute;
}
.product__item_empty .hexagon polygon {
  fill: none;
}
.product__item_empty:nth-of-type(8) {
  left: -14px;
  top: -1px;
}
.product__item_empty:nth-of-type(9) {
  top: 169px;
  left: 584px;
}
.product__item_empty:nth-of-type(10) {
  top: 500px;
  left: 394px;
}
.product__item:not(.product__item_empty):nth-of-type(1) {
  transform: translate(0px, 219px);
}
.product__item:not(.product__item_empty):nth-of-type(2) {
  transform: translate(-41px, 110px);
}
.product__item:not(.product__item_empty):nth-of-type(3) {
  transform: translate(-81px, 0px);
}
.product__item:not(.product__item_empty):nth-of-type(4) {
  transform: translate(0px, 239px);
}
.product__item:not(.product__item_empty):nth-of-type(5) {
  transform: translate(-41px, 129px);
}
.product__item:not(.product__item_empty):nth-of-type(6) {
  transform: translate(-81px, 18px);
}
.product__item:not(.product__item_empty):nth-of-type(7) {
  margin-left: auto;
  transform: translate(2px, -71px);
}
.product__item:not(.product__item_empty):hover .hexagon polygon {
  fill: #6fbcbc;
}
.product__item:not(.product__item_empty):hover .icon {
  fill: #fff;
}
