.form__agree {
  position: relative;
  cursor: pointer;
}
.form__agree .agree {
  position: absolute;
  visibility: hidden;
  opacity: 0;
}
.form__agree .agree.error + .form__agree-text:before {
  border-color: red;
}
.form__agree .agree:checked + .form__agree-text:after {
  opacity: 1;
}
.form__agree-text {
  font-size: 17px;
  position: relative;
  padding-left: 59px;
}
.form__agree-text:before {
  content: '';
  position: absolute;
  left: 0;
  top: -12px;
  width: 40px;
  height: 40px;
  border: 3px solid #6fbcbc;
}
.form__agree-text:after {
  content: '';
  position: absolute;
  left: 9px;
  top: 0;
  width: 20px;
  height: 13px;
  background-image: url("../img/icons/agree.png");
  opacity: 0;
  transition: 0.3s;
}
.form__agree-text a {
  text-decoration: underline;
}
.form__submit {
